html {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.body, p {
  margin: 0;
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-right: 0;
}

li {
  list-style-type: none;
}

.header {
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  align-items: center;
  height: 40px;
  padding: 10px 0;
  display: flex;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.container-grid {
  grid-template-rows: 1fr 1fr;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container-grid {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container-grid {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container-grid {
    grid-template-columns: repeat(4, 200px);
  }
}

.product-card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.product-card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product-card__name {
  text-align: left;
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card:hover .product-card__name {
  color: #34568b;
}

.product-card__code {
  text-align: left;
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__reviews {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.product-card__reviews-qty {
  color: #060b35;
  align-items: end;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.product-card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.product-card__price-text {
  text-align: left;
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.product-card__price-cost {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.product-card:hover {
  transition-duration: .3s;
  transform: scale(1.2);
}

.product-card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  width: 100%;
  height: 38px;
  transition-duration: .3s;
}

.stars {
  display: flex;
}

.stars :last-child {
  margin-right: 0;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
